<script>
  import { onMount, createEventDispatcher } from "svelte";
  import Button from "../components/Button.svelte";

  const dispatch = createEventDispatcher();

  let texts = [""];
  const script = `Do you want to accept the challenge?`;

  let showButtons = false;

  dispatch("complete");

  onMount(() => {
    type(script, text => (texts = text)).then(() => (showButtons = true));
  });
</script>

<style>p {
  text-align: left;
  line-height: 2rem;
  font-variant-ligatures: no-common-ligatures;
  margin: 30px; }
</style>

<div class="stage-0">
  {#each texts as text}
    <p>{text}</p>
  {/each}
  {#if showButtons}
    <Button>No</Button>
    <Button primary={true}>Yes</Button>
  {/if}
</div>
